import {
    GET_USER_INFO,
    LOGIN,
    LOGOUT, PING
} from "./actions.type";

import {
    SET_JTOKEN,
    SET_USER_DATA,
    CLEAR_USER_DATA, SET_PRICES_TYPE
} from "./mutations.type";

import {AuthApi} from "../../../api/auth";
import jToken from "../../../common/jToken"
import pricesTypes from "./prices.type"

export const state = {
    name: "",
    companyName: "",
    pricesType: "",
};

export const actions = {

    async [LOGIN]({commit}, payload) {
        let {login, password} = payload;

        if (!jToken.has()) {
            let res = await AuthApi.login(login, password);
            if (res) {
                let data = await res.json();
                commit(SET_USER_DATA, data.data);
                commit(SET_JTOKEN, data.token);
                return true
            }
        }

        return false
    },

    async [LOGOUT]({commit}) {
        if (jToken.has()) {
            let res = await AuthApi.logout();
            if (res) {
                commit(CLEAR_USER_DATA);
                return true
            }
        }

        return false;
    },

    async [GET_USER_INFO]({commit}) {
        if (jToken.has()) {
            let res = await AuthApi.getUserInfo();
            if (res) {
                let data = await res.json();
                commit(SET_USER_DATA, data);
            }
        }
    },

    async [PING]({commit}) {
        if (jToken.has()) {
            let res = await AuthApi.ping();
            if (res === null) {
                commit(CLEAR_USER_DATA);
            }
        }
    },
};

export const mutations = {
    [SET_USER_DATA](state, payload) {
        let {name, companyName} = payload;
        state.name = name;
        state.companyName = companyName;
    },

    [SET_JTOKEN](state, token) {
        if (jToken.isSupported()) {
            jToken.set(token);
        }
    },

    [SET_PRICES_TYPE](state, type) {
        if (localStorage) {
            localStorage.setItem('prices_type', type)
        }

        state.pricesType = type;
    },

    [CLEAR_USER_DATA](state) {
        state.name = "";
        state.companyName = "";
        jToken.delete()
    },
};

export const getters = {
    name(state) {
        return state.name;
    },

    companyName(state) {
        return state.companyName;
    },

    pricesType(state) {
        if (state.pricesType === '') {

            if (localStorage && localStorage.getItem("prices_type")) {
                state.pricesType = localStorage.getItem("prices_type")
            } else {
                state.pricesType = pricesTypes.PRICE_WHOLESALE
            }
        }

        return state.pricesType;
    }
};

export default {
    state,
    actions,
    mutations,
    getters,
    namespaced: true,
}