<template>
    <div v-if="isVisible">
        <section style="z-index: 17;position: fixed;">
            <transition name="fade">
                <LoadingSpinner class="center" v-show="isLoading"/>
            </transition>

            <transition name="fade">
                <img class="center" v-show="!isLoading" style="height: 70%;" ref="img"
                     @click="clickHandler"/>
            </transition>
        </section>
    </div>
</template>

<script>
    import {EventBus, Events} from "../../common/eventBus";
    import imageFetch from "../../common/imageFetch"
    import LoadingSpinner from "./LoadingSpinner";

    export default {
        name: "ImagePreviewContainer",

        components: {LoadingSpinner},

        data: () => {
            return {
                isVisible: false,
                isLoading: false,

                eventBus: null,
            }
        },

        methods: {
            clickHandler() {
                EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 15});
                this.hide();
                EventBus.$off(Events.onBackgroundClick);
            },

            async showImage(img) {
                this.isLoading = true;
                this.isVisible = true;

                EventBus.$emit(Events.showUnfocusedBackground, {zIndex: 16});
                EventBus.$on(Events.onBackgroundClick, () => {
                    EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 16});
                    this.hide();
                    EventBus.$off(Events.onBackgroundClick);
                });

                let rawRes = await imageFetch.download(img);
                let res = imageFetch.parse(await rawRes.arrayBuffer());
                this.isLoading = false;

                this.$nextTick(() => {
                    this.$refs.img.src = res;
                });
            },

            show() {
                this.isVisible = true;
            },

            hide() {
                this.isVisible = false
            },
        },

        created() {
            EventBus.$on(Events.showImagePreview, (img) => this.showImage(img));

            window.addEventListener('scroll', () => {
                if (this.isVisible) {
                    EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 16});
                    this.hide();
                    EventBus.$off(Events.onBackgroundClick);
                }
            });
        },

        destroyed() {
            EventBus.$off(Events.showImagePreview);
        },

        mounted() {
            this.eventBus = EventBus;
        },

        updated() {
            this.eventBus = EventBus;
        },
    }
</script>

<style lang="sass" scoped>

    .fade-enter-active, .fade-leave-active
        transition: 0.35s

    .fade-enter, .fade-leave-to
        opacity: 0

    img
        box-shadow: 0 14px 28px rgba(0, 0, 0, 0.52), 0 10px 10px rgba(0, 0, 0, 0.5)

    .center
        margin: auto
        position: fixed
        top: 0
        left: 0
        bottom: 0
        right: 0
</style>