const jToken = {
    isSupported: () => {
        return 'localStorage' in self
    },

    get: () => {
        return localStorage.getItem("jToken")
    },

    set: (token) => {
        localStorage.setItem("jToken", token);
    },

    has: () => {
        return localStorage.getItem("jToken") !== null && localStorage.getItem("jToken") !== undefined
    },

    delete: () => {
        localStorage.removeItem("jToken")
    }
};

export default jToken;