import config from "../common/config"
import myCacheStorage from "../common/cache"
import jToken from "../common/jToken"

const cache = myCacheStorage('api');

const APIService = {
    protectPostWithCaching(resource, params, lifeTime) {
        return this.requestWithCaching(resource, params, lifeTime, {method: 'post'})
    },

    protectedGetWithCaching(resource, lifeTime) {
        return this.requestWithCaching(resource, null, lifeTime, {method: 'get'})
    },

    requestWithCaching(resource, params, lifeTime, requestParams) {
        if (cache !== null) {
            let cacheId = resource + "/";
            if (params) {
                Object.entries(params).forEach(entry => {
                    let key = entry[0];
                    let value = entry[1];
                    cacheId += `${key}:${value};`;
                });
            }

            return cache.has(cacheId).then(has => {
                if (has) {
                    return cache.get(cacheId).then(res => res);
                } else {
                    return this.protected(resource, params, requestParams)
                        .then((res) => {
                            if (res) {
                                cache.add(cacheId, res.clone(), lifeTime);
                                return res
                            }
                            return null;
                        })
                }
            });
        } else {
            return this.protected(resource, params, requestParams);
        }
    },

    protectedPost(resource, params) {
        return this.protected(resource, params, {method: 'post'})
    },

    protected(resource, params, requestParams) {
        let requestSettings = {
            ...requestParams,
            headers: this.getHeaders(),
        };
        if (params) {
            requestSettings.body = JSON.stringify(params);
        }

        const url = config.PROTECTED_API_URL() + resource;

        return fetch(url, requestSettings)
            .then((res) => {
                if (res.ok) {
                    return res
                } else if (res.status === 401 ) {
                    jToken.delete();
                    document.location.reload();
                    return null
                } else {
                    console.error(res);
                    return null
                }
            })
    },


    postRequest(resource, params) {
        return this.request(resource, params, {method: 'post'})
    },

    request(resource, params, requestParams) {
        let requestSettings = {
            ...requestParams,
        };
        if (params) {
            requestSettings.body = JSON.stringify(params);
        }

        const url = config.API_URL() + resource;

        return fetch(url, requestSettings)
            .then((res) => {
                if (res.ok) {
                    return res
                } else {
                    console.error(res);
                    return null
                }
            })
    },

    getHeaders() {
        const headers = new Headers();
        headers.append('Access-Control-Allow-Origin', '*');
        headers.append('Authorization', jToken.get());

        return headers;
    }
};

export default APIService;