import {
    ADD_TO_BASKET, CLEAR_PRODUCTS, GET_MORE_PRODUCTS,
    GET_PRODUCTS,
    LOAD_CATEGORIES,
    RUN_PRODUCTS_UPDATER,
    SEARCH,
    SYNC_PRODUCTS_WITH_CACHE
} from "./actions.type";

import {
    CLEAR,
    ADD_TO_BASKET_M,
    REMOVE_FROM_BASKET,
    INCREMENT_BOUGHT_QUANTITY,
    SET_CATEGORIES,
    SET_LAST_CHOSEN_CATEGORY,
    SET_IS_BASKET_MINIMIZED,
    UPDATE_PRODUCTS,
    CHOOSE_CATEGORY,
} from "./mutations.type";

import {CatalogAPI} from "../../../api/catalog";
import {productUpdater} from "./WSProductsUpdater";
import myStorage from "../../../common/cache";
import modules from "../../all.modules.name"

const PRODUCTS_UPDATE_TIME = 10;
const cache = myStorage('api');

export const state = {
    basket: [],
    isBasketWindowMinimized: false,

    categories: [],
    chosenCategories: [],

    productUpdater: null,
    productUpdaterInitTimer: null,
};

export const actions = {
    async [LOAD_CATEGORIES]({commit}) {
        try {
            const response = await CatalogAPI.getAllCategories();
            if (response !== null) {
                let data = await response.json();
                commit(SET_CATEGORIES, data);
            }
        } catch (e) {
            console.error({e}, "while loading categories");
            return {err: e}
        }
    },

    async [GET_PRODUCTS]({commit}, payload) {
        try {
            let result = await CatalogAPI.getProductsByCategory(payload);

            if (result !== null) {
                let data = await result.json();
                return {data};

                //let payload = {categoryId, subCategoryId};

                /*if (!result.isFromCache) {
                    if (state.productUpdaterInitTimer) {
                        clearTimeout(state.productUpdaterInitTimer);
                    }

                    state.productUpdaterInitTimer = setTimeout(() => dispatch(RUN_PRODUCTS_UPDATER, payload), PRODUCTS_UPDATE_TIME * 1000);
                } else {
                    dispatch(RUN_PRODUCTS_UPDATER, payload)
                }*/
            }
            return null;

        } catch (e) {
            console.error({e}, "while loading products");
            return {err: e}
        }
    },

    async [GET_MORE_PRODUCTS](vuexPayload, payload) {
        try {

            let {itemsPerPage} = payload;
            let result = await CatalogAPI.getMoreProductsByCategory(payload);

            if (result !== null) {
                let data = await result.json(),
                    hasMore = data.length === itemsPerPage;

                return {data, hasMore}
            }

            return null

        } catch (e) {
            console.error({e}, "while loading more products");
            return {data: [], hasMore: false};
        }
    },

    async [SEARCH]({state}, request) {
        try {
            const result = await CatalogAPI.search(request);
            if (result !== null) {
                let data = await result.json();
                return {data};

                /*let childrenIds = [];
                data.forEach((item) => {
                    childrenIds = [...Object.keys(item.childrenData), ...childrenIds];
                });

                /*if (!result.isFromCache) {
                    if (state.productUpdaterInitTimer) {
                        clearTimeout(state.productUpdaterInitTimer);
                    }
                    state.productUpdaterInitTimer = setTimeout(() => dispatch(RUN_PRODUCTS_UPDATER, {additional: childrenIds}),
                        PRODUCTS_UPDATE_TIME * 1000);
                } else {
                    dispatch(RUN_PRODUCTS_UPDATER, {additional: childrenIds})
                }*/
            }
        } catch (e) {
            console.error({e}, "while search");
            return {err: e}
        }
    },

    async [RUN_PRODUCTS_UPDATER]({commit, dispatch, state}, payload) {
        let downloadAndUpdateData = async () => {
            let res = await state.productUpdater.sendGetData();
            commit(UPDATE_PRODUCTS, res);
            await dispatch(SYNC_PRODUCTS_WITH_CACHE, state.productUpdater.handshakePayload);
            state.productUpdater.timer = setTimeout(() => dispatch(RUN_PRODUCTS_UPDATER), PRODUCTS_UPDATE_TIME * 1000);
        };

        if (state.productUpdaterInitTimer) {
            state.productUpdaterInitTimer = null;
        }

        try {
            if (payload) {
                if (!state.productUpdater || state.productUpdater.isClosed) {
                    state.productUpdater = productUpdater;
                    await state.productUpdater.start();
                    await state.productUpdater.sendHandshake(payload);
                    await downloadAndUpdateData();
                } else if (state.productUpdater && state.productUpdater.isWorking()) {
                   await downloadAndUpdateData();
                }
            } else if (state.productUpdater && state.productUpdater.isWorking()) {
                await downloadAndUpdateData();
            }
        } catch (e) {
            console.error(e);

            if (state.productUpdater) {
                state.productUpdater.close();
            }
        }
    },

    [CLEAR_PRODUCTS]({commit, state}) {
        if (state.productUpdater) {
            state.productUpdater.close();
            state.productUpdater = null;
        }

        if (state.productUpdaterInitTimer) {
            clearTimeout(state.productUpdaterInitTimer);
            state.productUpdaterInitTimer = null;
        }

        commit(CLEAR_PRODUCTS);
    },

    async [SYNC_PRODUCTS_WITH_CACHE]({commit, state}, payload) {
        let {categoryId, subCategoryId} = payload;

        if (!cache) return;

        if (categoryId) {
            let cacheId;

            if (subCategoryId) {
                cacheId = `/catalog/getProductsByCategory/categoryId:${categoryId};subCategoryId:${subCategoryId};`
            } else {
                cacheId = `/catalog/getProductsByCategory/categoryId:${categoryId};`
            }

            let response = await cache.get(cacheId);
            if (response) {
                console.log("updated json", state.products);
                await cache.updateJSON(cacheId, state.products);
            }
        }

        if (state.search.results && state.search.results.length > 0) {
            let cacheId = `/catalog/search/request:${state.search.lastRequest};`;
            let response = await cache.get(cacheId);
            if (response) {
                console.log("updated json", state.search.results);
                await cache.updateJSON(cacheId, state.search.results);
            }
        }
    },

    [ADD_TO_BASKET]({commit, state}, item) {
        let basket = state.basket;

        for (let i = 0;i < basket.length; i++) {
            let basketItem = basket[i];

            if (basketItem.id === item.id) {
                commit(INCREMENT_BOUGHT_QUANTITY, i);
                return;
            }
        }

        commit(ADD_TO_BASKET_M, item);
    }
};

export const mutations = {
    [SET_CATEGORIES](state, cat) {
        state.categories = cat
    },

    [SET_LAST_CHOSEN_CATEGORY](state, data) {
        state.lastChosenCategory = data;
    },

    [SET_IS_BASKET_MINIMIZED](state, payload) {
        state.isBasketWindowMinimized = payload;
    },

    [CHOOSE_CATEGORY](state, chosen) {
        let res = state.categories.find((el) => {
            return el.id === chosen;
        });

        if (!res)
            return;

        if (res.isExpanded !== undefined && res.isExpanded != null) {
            res.isExpanded = !res.isExpanded;
        } else {
            res.isExpanded = true;
        }
    },

    [UPDATE_PRODUCTS](state, data) {
        let products;

        if (state.products.length > 0) {
            products = state.products;
        } else if (state.search.results.length > 0) {
            products = state.search.results;
        }

        if (!products)
            return;

        for (let itemId in data) {

            for (let i = 0; i < products.length; i++) {

                for (let prId in products[i].childrenData) {

                    if (prId === itemId) {
                        if (data[itemId].isCountInStockTooBig) {
                            products[i].childrenData[prId].isCountInStockTooBig = true;
                        }

                        products[i].childrenData[prId].countInStock = data[itemId].countInStock;
                        break
                    }
                }
            }
        }
    },

    [ADD_TO_BASKET_M](state, item) {
        item.boughtQuantity = 1;
        item.isValid = true;
        state.basket.push(item);
    },

    [REMOVE_FROM_BASKET](state, index) {
        state.basket.splice(index, 1);
    },

    [INCREMENT_BOUGHT_QUANTITY](state, index) {
        state.basket[index].boughtQuantity++;
    },

    [CLEAR](state) {
        state.basket = [];
    },

    [CLEAR_PRODUCTS](state) {

    },
};

export const getters = {
    categories: state => state.categories,

    chosenCategory: state => state.chosenCategory,

    lastChosenCategory: state => state.lastChosenCategory,

    basket: state => state.basket,

    isBasketWindowMinimized: state => state.isBasketWindowMinimized,

    isBasketValid(state) {
        let basket = state.basket;

        for (let i = 0; i < basket.length; i++) {
            if (!basket[i].isValid || !basket[i].boughtQuantity) {
                return false;
            }
        }

        return true;
    },

    basketTotalPrice: (state, getters, rootState, rootGetters) => {
        let pricesType = rootGetters[modules.user + '/pricesType'];

        if (getters.isBasketValid) {
            let res = 0,
                basket = state.basket;

            for (let i = 0; i < basket.length; i++) {
                let price;
                if (pricesType in basket[i]) {
                    price = basket[i][pricesType];
                } else {
                    price = basket[i].price;
                }

                res += price * basket[i].boughtQuantity;
            }

            return Math.round(res * 1000) / 1000;
        }

        return 0;
    }
};

export default {
    namespaced: true,
    state,
    actions,
    mutations,
    getters,
}