import APIService from "./index";

export const AuthApi = {

    login: (login, password) => {
        return APIService.postRequest("/auth/login", {login, password})
    },

    logout: () => {
        return APIService.protectedPost("/auth/logout", {})
    },

    getUserInfo: () => {
        return APIService.protectPostWithCaching("/auth/userInfo", {}, 1800)
    },

    ping: () => {
        return APIService.protectedPost("/auth/ping", {})
    },
};